.loader::after {
  left: 50%;
  top: 50%;
  z-index: 11;
  border: clamp(15px, 2.5vw, 30px) solid var(--br-c);
  border-top-color: transparent;
  border-radius: 50%;
  width: clamp(120px, 16.667vw, 200px);
  height: clamp(120px, 16.667vw, 200px);
  transform: translate(-50%, -50%);
  transition-property: transform;
  animation: rotate360 1.5s ease-in-out infinite;
}

.mobile-menu {
  position: fixed;
  inset: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
  overflow: auto;
  overflow-x: hidden;
  border-radius: 0 0 20% 20%;
  background-color: rgba(var(--bg-e), 1);
  opacity: 0;
  transform: translateY(-100%);
  transition: all 0.3s ease-out;
}
.mobile-menu__close {
  position: absolute;
  right: clamp(15px, 3.26vw, 25px);
  top: clamp(15px, 3.26vw, 25px);
}
.mobile-menu.active {
  border-radius: 0;
  opacity: 1;
  transform: translate(0%);
}

.modal {
  border-radius: 16px;
  width: 95vw;
  max-width: 970px;
}